import React, { Component } from 'react'; 
import {List,Loader} from 'semantic-ui-react'

class WeatherDetail extends Component {
    render() {
      return (
        <div>
             <div  hidden={this.props.loading==null || this.props.loading}>
                <List size="huge">
                    <List.Item><b>Location:</b>  {this.props.location}</List.Item>
                    <List.Item><b>Temp_c:</b>  {this.props.temp_c}</List.Item>
                    <List.Item><b>Humidity:</b>  {this.props.humidity}</List.Item>
                    <List.Item><b>Condition:</b>  {this.props.condition}</List.Item>
                </List>
            </div>
            <Loader  active={this.props.loading} inline='centered' />
        </div>
      )
    }
}
export default WeatherDetail;