import React, { Component } from 'react';
import logo from '../assets/images/logo_edrans.png';

class HeaderApp extends Component {
  render() {
    return (
          <header className="six wide column center aligned middle aligned grid">
            <img className="ui fluid" src={logo}  alt="logo" />
            <h1 className="ui icon header">
                Weather Finder
                <div className="sub header">Find out the temperature, weather conditions and more.</div>
            </h1>  
          </header>
  );
  }
}
 
export default HeaderApp;