import React, { Component } from 'react'; 

import WeatherFinder from './weather/finder';
class MainApp extends Component {
  render() {
    return (
        <main className="ten wide column aligned middle aligned grid">
            <div className="ui  middle aligned grid panel">
            <div className="column" >
                <WeatherFinder></WeatherFinder>
            </div> 
            </div> 
        </main>
  );
  }
}
export default MainApp;