import React, { Component } from 'react'; 
import {Form,Input} from 'semantic-ui-react'
import {ApiCaller} from '../caller'
import WeatherDetail  from './detail'
 
class WeatherFinder extends Component {
    constructor(props) {
        super(props);
        this.state = { city: '', 
                       country: '' ,
                       location:"",
                       temp_c:"",
                       humidity:"",
                       condition:"",
                       loading:null,
                       error:null
                    };
    }
    handleChange = (e, { name, value }) => this.setState({ [name]: value })
    handleSubmit = () => {
        const { city, country } = this.state
        let checkWeather=(res) => {
            this.setState({location:res.location.name,
                            temp_c:res.current.temp_c,
                            humidity:res.current.humidity,
                            condition:res.current.condition.text,
                            loading: false});
        }
        let callback_error=(error) => {
            console.log(error)
            this.setState({location:error.message,
                temp_c:"",
                humidity:"",
                condition:"",
                loading: false});
            
        }
        this.setState({ loading: true})
        // var url='http://api.openweathermap.org/data/2.5/weather?q=London,uk&APPID=bf323b133af7ff6a6c87abae9f8ee9a7'
        var url="http://api.apixu.com/v1/current.json?key=b5d60621231c4620a4615445181612&q="+city+"&country="+country
        new ApiCaller().get(url,checkWeather,callback_error);
    }
    render() {
      const { city, country} = this.state
      return (
          <div>
          <Form onSubmit={this.handleSubmit}  widths='equal'>
            <Form.Group>
                <Form.Field>
                    <Input    fluid placeholder='Insert city...' name='city'   value={city} onChange={this.handleChange}/>
                </Form.Field>
                <Form.Field>
                    <Input    fluid placeholder='Insert country...' name='country' value={country} onChange={this.handleChange}/>
                </Form.Field>
                <Form.Field  align="right" >
                    <Form.Button   content='Search Weather'  secondary   />
                </Form.Field>
            </Form.Group>
          </Form>
          <WeatherDetail loading ={this.state.loading}
                         location={this.state.location} 
                         temp_c={this.state.temp_c}
                         humidity={this.state.humidity}
                         condition={this.state.condition}></WeatherDetail>
          </div>
      )
    }
}
export default WeatherFinder;